import React, { useState } from 'react';
import Menu from '../components/Menu';  // 
import { Button, Toast ,Card } from 'antd-mobile';
import { AntOutline,  } from 'antd-mobile-icons'
import './DroneControl.scss';  //  Less 
import axios from '../utils/axios'; //  axios 
import { useNavigate } from 'react-router-dom';
const HomePage = () => {
    const navigate = useNavigate(); // 
    const [loading, setLoading] = useState(false);
    const [account, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const items = [
      { id: 1, title: '', description: '',url:'https://www.bing.com',img:'https://www.bing.com' },
      { id: 2, title: '', description: '',url:'https://www.bing.com',img:'https://www.bing.com' },
      { id: 3, title: '', description: '',url:'https://www.bing.com',img:'https://www.bing.com' },
    ]
    const handleDirection = async (direction: string) => {
      console.log(`: ${direction}`);
      setLoading(true);
      // 
      try {
        //  API 
        const response = await axios.post('/api/sysUser/login', { account, password,type:1      });
        if (response.data.code===200) {
          const token = response.data.data.token;  //  token  response.data.token 
          localStorage.setItem('token', token);  //  localStorage
          Toast.show('');
          // 
          navigate('/');
          // 
        } else {
          Toast.show('');
        }
      } catch (error) {
        Toast.show('');
      } finally {
        setLoading(false);
      }
    };
  return (
    <div>
      <h1></h1>

      <div className="container fc_s_c">
      {items.map((item) => (
        <a
        href={item.url}
        key={item.id}
        target="_blank"
        rel="noopener noreferrer"
        style={{ textDecoration: 'none', color: 'inherit'  }}
      >
        <Card >
        <div  className='fr_c_c'>
              <img 
                src={item.img} 
                alt={item.title} 
                style={{ width: 50, height: 50, marginRight: 12, borderRadius: 4 }}
              />
              <div>
                <div style={{ fontWeight: 'bold' }}>{item.title}</div>
                <div>{item.description}</div>
              </div>
            </div>
        </Card>
        </a>
      ))}
      </div>
    </div>
  );
};

export default HomePage;
